/**
 * 首页头部组件
 */
import React from 'react';
import { getPosition } from '../../utils'
import style from './index.scss'
export default class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      city: '北京市'
    }
  }

  async componentWillMount() {
    // 获取定位信息
    getPosition((position) => {
      this.setState({
        city: position.city
      });
    });
  }

  render() {
    return (
      <header className={style.header} >
        <span className={`${style['icon-font']} ${style['icon-location']}`}>{this.state.city}</span>
        <div className={`${style['icon-font']} ${style['icon-search']}`}><input type="search" placeholder="白菜" /></div>
        <span className={`${style['icon-font']} ${style['icon-user']}`}></span>
      </header>
    )
  }
}