/**
 * 商品列表
 */
import React from 'react';
import Filter from '../../components/filter/'
import CardList from '../../components/cardList/'
import Loading from '../../components/loading/'
import { goods } from '../../api/'
import { logic } from '../../utils/'
import style from './index.scss'

export class GoodsList extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      goods: [],
      loading: true
    }
  }

  componentWillMount() {
    goods({
      page: 1,
      size: 10
    })
      .then((res) => {
        logic(res, (data) => {
          this.setState({
            goods: data.data
          });
        })
      })
      .catch((error) => {
        console.log(error.message);
      })
      .finally(() => {
        this.setState({
          loading: false
        });
      })
  }

  render() {
    return (
      <div>
        <Filter />
        <section className={style['spacing-box']}>
          <CardList data={this.state.goods} />
          <Loading loading={this.state.loading} />
        </section>
      </div>
    );
  }
}

export default (props) => <GoodsList {...props} key={props.location.pathname} />