import React from 'react';
import { shopTags } from '../../../store/dictionary';
import style from './index.scss'

export default class GoodsInfo extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            collection: !!this.props.collection
        };
        this.collectionToggle = this.collectionToggle.bind(this);
    }

    collectionToggle() {
        this.setState({
            collection: !this.state.collection
        });
    }

    render() {
        let tagLable = shopTags.filter(item => this.props.tag === item.code).map((item, index) => {
            return item.lable;
        });

        return (
            <div className={style['goods-info']}>
                <div className={style['price-box']}>
                    <span className={style['goods-price']}>{this.props.price}元/斤</span>
                    <span className={style['market-price']}><del>市场价{this.props.marketPrice}元/斤</del></span>
                    <i onClick={this.collectionToggle} className={style['icon-font'] + " " + (this.state.collection ? style['icon-star-hollow'] : style['icon-star-solid'])} />
                </div>
                <div>
                    <p className={style['goods-summary']}>{this.props.summary}</p>
                    <p className={style['business-tag']}>
                        <i className={`${style['icon-font']} ${style['icon-credit']}`}>
                            {tagLable}
                        </i>
                    </p>
                </div>
            </div>
        )
    }
}