/**
 * 商品列表组件
 */
import React from 'react';
import { Link } from 'react-router-dom';
import { shopTags } from '../../store/dictionary';
import style from './index.scss'

class CardItem extends React.Component {
    render() {
        let tagNodes = shopTags.filter(item => this.props.item.tag === item.code).map((item, index) => {
            return (
                <i className={style.lxsj} key={index}>
                    {item.lable}
                </i>
            );
        });

        return (
            <figure className={style.item}>
                <Link to={'/goods/'+this.props.item.id}>
                    <img src={this.props.item.src} alt={this.props.item.title} />
                    <div className={style['item--right']}>
                        <figcaption>
                            {this.props.item.title}
                        </figcaption>
                        <p className={style.intro}>{this.props.item.summary}</p>
                        <p className={style['sale-msg']}>
                            <span>{this.props.item.price}元</span>
                            <span className={style.tag}>{tagNodes}</span>
                            <small>{this.props.item.distance}人购买</small>
                        </p>
                    </div>
                </Link>
            </figure>
        );
    }
}

export { CardItem as default }