/**
 * 商品列表集合
 */
import React from 'react';
import CardItem from '../cardItem/'

class CardList extends React.Component {

    render() {
        let itemNodes = this.props.data.map(item => {
            return (
                <CardItem
                    item={item}
                    key={item.id}
                />
            );
        });
        return (
            <ul>
                {itemNodes}
            </ul>
        )
    }
}

export { CardList as default };