/**
 * 评论组件
 */

import React from 'react';
import Swiper from 'react-id-swiper/lib/ReactIdSwiper.full';
import 'react-id-swiper/src/styles/css/swiper.css'
import Zmage from 'react-zmage'
import Stars from '../../../components/stars/'
import style from './index.scss'

export default class Comment extends React.Component {
    render() {

        const params = {
            slidesPerView: "auto",
            watchSlidesVisibility: true,
            spaceBetween: 10,
            freeMode: true
        },
            slider = this.props.photoArray.map((item, index) => {
                return (
                    <Zmage backdrop="rgba(0,0,0,0.6)" src={item} className={style.photo} key={index} />
                )
            });


        return (
            <div className={style.comment}>
                <div className={style.info}>
                    <img alt="avatar" src={this.props.avatar} className={style.avatar} />
                    <div className={style['name-wraper']}>
                        <p className={style.name}>{this.props.userName}</p>
                        <Stars grade={this.props.grade} max={5} />
                        <time>{this.props.date}</time>
                    </div>
                </div>
                <p className={style.message}>
                    {this.props.comment}
                </p>
                {
                    this.props.photoArray.length > 0 &&
                    <div className={style['photo-wraper']}>
                        <Swiper {...params}>
                            {slider}
                        </Swiper>
                    </div>
                }

            </div>

        );
    }
}