/**
 * 走马灯组件
 */
import React from 'react';
import Swiper from 'react-id-swiper/lib/ReactIdSwiper.full';
import 'react-id-swiper/src/styles/css/swiper.css'
import style from './index.scss';

export default class Carousel extends React.Component {

    render() {
        const items = this.props.src.map((item, index) => {
            return (
                <div key={index} className={style.item}>
                    <img
                        alt="img"
                        data-src={item}
                        className={style['swiper-lazy']}
                    />
                    <div className="swiper-lazy-preloader swiper-lazy-preloader-white" />
                </div>
            );
        }),
            params = {
                lazy: {
                    elementClass: style['swiper-lazy'],
                    loadedClass: style['swiper-lazy-loaded']
                },
                loop: true,
                centeredSlides: true,
                pagination: {
                    el: '.swiper-pagination'
                }
            };


        return (
            <div className={style['swiper-box']}>
                <Swiper {...params}>
                    {items}
                </Swiper>
                <div className={style.shadow}>
                    <h2>{this.props.title}</h2>
                    <p className={style.slogan}>{this.props.slogan}</p>
                </div>
            </div >
        );
    }
}