/**
 * 商品详情
 */
import React from 'react';
import Header from '../../components/childHeader'
import Carousel from './carousel'
import GoodsInfo from './goodsInfo'
import BoxTitle from '../../components/boxTitle/'
import Comment from './comment'
import { goodsInfo } from '../../api/'
import { logic } from '../../utils/'
import style from './index.scss'

export default class Goods extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            goods: {
                id: '',
                src: [],
                title: '',
                slogan: '',
                price: '',
                marketPrice: '',
                summary: '',
                tag: '',
                collection: 0,
                comments: []
            }
        }
    }

    componentWillMount() {
        goodsInfo({
            id: 1,
        })
            .then((res) => {
                logic(res, (data) => {
                    this.setState({
                        goods: data
                    });
                })
            })
            .catch((error) => {
                console.log(error.message);
            })
    }

    render() {
        return (
            <div className={style.container}>
                <Header />
                <div className={style['spacing-box']}>
                    {
                        this.state.goods.src.length ? <Carousel {...this.state.goods} /> : null
                    }
                    <GoodsInfo key={this.state.goods.id} {...this.state.goods} />
                </div>
                <div className={style['spacing-box']}>
                    <BoxTitle icon='icon-comment' title='评论' />
                    {
                        this.state.goods.comments.length > 0 ? this.state.goods.comments.map((item, index) => <Comment key={index} {...item} />) : null
                    }
                </div>
            </div>
        )
    };
}