import React from 'react';
import style from './index.scss'
export default class Filter extends React.Component {

    constructor(props){
        super(props)
        this.state = {
            active:0
        }
    }

    handleClick(index){
        this.setState({ active: index });
    }   
    
    render() {
        const nodes = ['距离', '价格', '好评'].map((item, index) => {
            return (
                <li key={item} className={index === this.state.active ? style.active : null} onClick={() => this.handleClick(index)}>
                    {item}
                </li>
            );
        });

        return (
            <ul className={style.filter}>
                {nodes}
            </ul>
        )
    }

}