/**
 * 子页头部组件
 */
import React from 'react';
import { Link } from "react-router-dom";
import { TransitionMotion, spring, presets } from 'react-motion';
import style from './index.scss'

export default class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            show: false,
        }
        this.handleBack = this.handleBack.bind(this);
        this.clickHandler = this.clickHandler.bind(this);
    }

    handleBack() {
        // 返回方法
        window.location.href = '/';
    }

    willEnter() {
        return {
            opacity: 0,
            translate3d: 0.6
        }
    }

    willLeave() {
        return {
            opacity: spring(0),
            translate3d: spring(0.6)
        }
    }

    clickHandler() {
        this.setState({
            show: !this.state.show
        })
    }

    clickUserCenter() {
        alert('这个不给点😝');
    }

    componentDidMount() {
        this.setState({
            show: false
        })
    }

    render() {
        return (
            <header className={style.header}>
                <span className={`${style['icon-font']} ${style['icon-back']}`} onClick={this.handleBack}></span>
                <p className={style.title}>商品详情</p>
                <span className={`${style['icon-font']} ${style['icon-menu']}`} onClick={this.clickHandler}></span>

                <TransitionMotion styles={this.state.show ? [
                    {
                        key: 'menu',
                        style: {
                            opacity: spring(1),
                            translate3d: spring(0.8, presets.wobbly)
                        }
                    }
                ] : []}
                    willEnter={this.willEnter}
                    willLeave={this.willLeave}>
                    {inStyles => (
                        inStyles[0] ? (
                            <dl key={inStyles[0].key} style={{
                                opacity: `${inStyles[0].style.opacity}`,
                                transform: `translate(0,${inStyles[0].style.translate3d}rem)`
                            }} className={style['header--layer']}>
                                <dt>
                                    <Link to="/">首页</Link>
                                </dt>
                                <dt>
                                    <a href="###" onClick={this.clickUserCenter}>个人中心</a>
                                </dt>
                            </dl>
                        ) : null
                    )}
                </TransitionMotion>
            </header>
        );
    }
}