import React from 'react';
import style from './index.scss'

export default class Star extends React.Component {
    render() {
        return (
            this.props.solid ? <i className={`${style['icon-font']} ${style['icon-star-solid']}`}></i> : <i className={`${style['icon-font']} ${style['icon-star-hollow']}`}></i> 
        )
    }
}