// 页面模板
import Index from '../views/index'
import Home from '../views/home'
import GoodsList from '../views/goodsList'
import Goods from '../views/goods'

const routes = [
    {
        path: '/home',
        component: Index,
        children: [
            {
                path: '/home/',
                component: Home,
                exact: true
            },
            {
                path: '/home/goodsList/:id',
                component: GoodsList,
                exact: true
            }
        ]
    },
    {
        path: '/goods/:id',
        component: Goods,
        exact: true
    }
]

export default routes