import fetch from './fetch';

//登录接口
export const login = (params) => fetch({
    url: '/login',
    method: 'post',
    data: params
});

// 商品列表
export const goods = (params) => fetch({
    url: '/goodsQuery',
    method: 'post',
    data: params
});

// 商品详情
export const goodsInfo = (params) => fetch({
    url: '/goodsInfo',
    method: 'get',
    params: params
});