/**
 * 根文件
 */
import { BrowserRouter as Router, Route, Redirect } from "react-router-dom";
import React from 'react';
import routes from './router'

class App extends React.Component {

  componentWillMount() {
    viewAdaptation();
    window.onresize = function () {
      viewAdaptation();
    }
  }

  render() {


    return (
      <Router>
        <Route
          exact
          path="/"
          render={() => (
            <Redirect
              to={{
                pathname: "/home",
              }}
            />
          )}
        />
        {
          routes.map((route, index) => {
            if (route.exact) {
              return <Route exact key={index} path={route.path} render={
                props => (
                  <route.component  {...props} children={route.children} />
                )
              } />
            } else {
              return <Route key={index} path={route.path} render={
                props => (
                  <route.component  {...props} children={route.children} />
                )
              } />
            }
          })
        }
      </Router>

    )
  }
}

function viewAdaptation() {
  let rootEle = document.documentElement,
    viewWidth = rootEle.clientWidth,
    scale = (viewWidth / 640 * 100).toFixed(1);
  rootEle.style.fontSize = (scale > 100 ? 100 : scale) + 'px';
}

export default App;