/**
 * 模块标题组件
 */
import React from 'react'
import style from './index.scss'
class BoxTitle extends React.Component {
    render(){
        return (
            <header className={style.header}><i className={style['icon-font'] + ' ' + style[this.props.icon]}></i>{this.props.title}</header>
        );
    }
}

export { BoxTitle as default}